<!--  -->
<template>
    <div class="todo-footer-box" v-if="todos.length>0">
        <p>
            <label>
                <!-- isSelectAll 监视 isSelectAll读取它的值，并且根据它的值去改变其他项-->
                <input type="checkbox" v-model="isSelectAll">
            </label>
        
            <span style="margin-left:10px;">已完成 {{completeNum}}</span> / 
            <span>全部 {{totalCount}}</span>
        </p>
        <span class="del-all-btn" v-show="completeNum" @click="delCompleted">清除已完成任务</span>
    </div>
</template>

<script>
import { mapGetters,mapActions,mapState } from 'vuex'
export default {
data () {
    return {
        // isSelectAll:false
    }
},

// props: {
//     list:Array,
//     selectAll:Function,
//     delCompleted:Function
// },

components: {},

created () {},

computed: {
    ...mapState(['todos']),
    ...mapGetters(['totalCount','completeNum','isSelectAll']),
    
    // completeNum(){
    //     //遍历数组如果选中则相加，默认选中数量为0
    //     return this.list.reduce((total,item) => total + (item.complete?1:0),0)  
    // },
    isSelectAll:{
        get(){
            // return this.completeNum === this.list.length && this.completeNum>0
            return this.$store.getters.isSelectAll
        },
        set(value){  // value就是当前checkbox最新的布尔值
            // this.selectAll(value)
            console.log('value-----',value)
            this.$store.dispatch('selectAll',value)
        }
    },
},

mounted (){},

methods: {
    ...mapActions(['delCompleted']),
}
}

</script>
<style>
.todo-footer-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.del-all-btn{
    padding: 5px 10px;
    box-sizing: border-box;
    color: #fff;
    font-size: 15px;
    background-color: rgb(226, 64, 43);
    border-radius: 5px;
    cursor: pointer;
}
</style>
