<!-- List -->
<template>
    <div class="todo-list-box">
        <ul class="todo-list-ul">
            <template v-if="todos.length">
                <list-item v-for="(item,index) in todos" :key="index" :item="item" :index="index"/>
            </template>
            <template v-else>暂时没有任务  Q-Q</template>
        </ul>
    </div>
</template>

<script>
import ListItem from './ListItem'
import { mapState } from 'vuex'
import storageManage from '../../util/storageManage'
export default {
data () {
    return {
    }
},

// props:{
//     list:Array,
//     delItem:Function
// },


components: {
    ListItem
},

created () {},

computed: {
    ...mapState(['todos'])
},

watch:{
    todos:{  // 监视todos的所有
        deep:true,
        handler:storageManage.saveTodos
    }
},

mounted (){},

methods: {}
}

</script>
<style>
.todo-list-box{
    margin-top: 20px;
    width: 100%;
    border: 1px solid gray;
}
.todo-list-ul{
    margin: 0;
    padding: 20px 0;
    box-sizing: border-box;
    text-indent: 20px;
}
</style>
